import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import Slider from 'react-slick'

import {
  Wrapper,
  Phone,
  Information,
  InformationWrapper,
  Item,
  StyledImg,
} from './style'

const KnowWallet = ({ language }) => {
  const { t } = useTranslation()

  const [active, setActive] = useState(0)

  const handleSlideChange = index => {
    setActive(index)
  }

  let slider = React.createRef()
  const settings = {
    arrows: false,
    autoplay: true,
    className: 'slider_wallet',
    cssEase: 'cubic-bezier(1, -0.5, 0.5, 1)',
    dots: false,
    speed: 500,
    slide: 'img',
    slidesToShow: 1,
    slidesToScroll: 1,
    afterChange: handleSlideChange,
    responsive: [
      {
        breakpoint: 770,
        settings: {
          dots: false,
        },
      },
    ],
  }
  return (
    <Wrapper>
      <Phone>
        <Slider ref={sl => (slider = sl)} {...settings}>
          {language === 'es' && (
            <StyledImg src='../../images/img1.png' alt='Slider' />
          )}
          {language === 'es' && (
            <StyledImg src='../../images/img2.png' alt='Slider' />
          )}
          {language === 'es' && (
            <StyledImg src='../../images/img3.png' alt='Slider' />
          )}
          {language === 'es' && (
            <StyledImg src='../../images/img4.png' alt='Slider' />
          )}
          {language === 'en' && (
            <StyledImg src='../../images/enimg1.jpg' alt='Slider' />
          )}
           {language === 'en' && (
            <StyledImg src='../../images/enimg2.jpg' alt='Slider' />
          )}
           {language === 'en' && (
            <StyledImg src='../../images/enimg3.jpg' alt='Slider' />
          )}
           {language === 'en' && (
            <StyledImg src='../../images/enimg4.jpg' alt='Slider' />
          )}
        </Slider>
      </Phone>
      <Information>
        <InformationWrapper>
          <Item
            active={active === 0}
            onClick={() => {
              slider.slickGoTo(0)
            }}
          >
            {active === 0 ? (
              <img
                className='active'
                src='../../images/phone1.svg'
                alt='Mide tu huella'
              />
            ) : (
              <img src='../../images/phone1_off.svg' alt='Mide tu huella' />
            )}
            <h2>
              1. {t('Mide')} <br />
              {t('tu huella')}
            </h2>
            <p>{t('Ingresa los datos de uno ó multiples viajes')}.</p>
          </Item>
          <Item
            active={active === 1}
            onClick={() => {
              slider.slickGoTo(1)
            }}
          >
            {active === 1 ? (
              <img
                className='active'
                src='../../images/phone2_on.svg'
                alt='Compensa tu impacto'
              />
            ) : (
              <img src='../../images/phone2.svg' alt='Compensa tu impacto' />
            )}
            <h2>
              2. {t('Compensa')} <br />
              {t('tu impacto')}
            </h2>
            <p>
              {t('Elige un proyecto de triple impacto y neutraliza tu huella')}.
            </p>
          </Item>
          <Item
            active={active === 2}
            onClick={() => {
              slider.slickGoTo(2)
            }}
          >
            {active === 2 ? (
              <img
                className='active'
                src='../../images/phone3_on.svg'
                alt='Certifica tu aporte'
              />
            ) : (
              <img
                className='inactive'
                src='../../images/phone3.svg'
                alt='Certifica tu aporte'
              />
            )}
            <h2>
              3. {t('Certifica')} <br />
              {t('tu aporte')}
            </h2>
            <p>{t('Genera un certificado único, inmutable y verificable')}.</p>
          </Item>
          <Item
            active={active === 3}
            onClick={() => {
              slider.slickGoTo(3)
            }}
          >
            {active === 3 ? (
              <img
                className='active'
                src='../../images/phone4_on.svg'
                alt='Compartí tu acción'
              />
            ) : (
              <img src='../../images/phone4.svg' alt='Compartí tu acción' />
            )}
            <h2>
              4. {t('Comparte')} <br />
              {t('tu acción')}
            </h2>
            <p>{t('Contale al mundo que neutralizar un viaje es posible!')}</p>
          </Item>
        </InformationWrapper>
      </Information>
    </Wrapper>
  )
}
export default KnowWallet
