import React from 'react'
import { graphql } from 'gatsby'

import { BaseCSS } from 'styled-bootstrap-grid'
import Layout from '../layout'
import { StyledCol, StyledRow } from '../components/style'
import Header from '../components/Header'
import Step1 from '../components/Step1'
import Step2 from '../components/Step2'
import Step3 from '../components/Step3'
import Step4 from '../components/Step4'
import Step5 from '../components/Step5'
import Step6 from '../components/Step6'
import Step7 from '../components/Step7'
import Step8 from '../components/Step8'
import Step10 from '../components/Step10'

import SEO from '../components/seo'

const IndexPage = ({ pageContext }) => {
  const { language } = pageContext

  const somos = React.createRef()
  const hacemos = React.createRef()

  const handleWhoare = () => {
    somos.current.scrollIntoView({
      behavior: 'smooth',
      block: 'start',
    })
  }
  const handleDo = () => {
    hacemos.current.scrollIntoView({
      behavior: 'smooth',
      block: 'start',
    })
  }
  return (
    <Layout>
      <SEO title='Neutralfly' />
      <BaseCSS />
      <StyledRow>
        <StyledCol col={12} noGutter={true}>
          <Header handleWhoare={handleWhoare} handleDo={handleDo} />
        </StyledCol>
        <StyledCol col={12} noGutter={true}>
          <Step1 />
        </StyledCol>
        <StyledCol col={12} noGutter={true}>
          <Step2 />
        </StyledCol>
        <StyledCol col={12} noGutter={true}>
          <Step3 />
        </StyledCol>
        <StyledCol ref={hacemos} col={12} noGutter={true}>
          <Step4 />
        </StyledCol>
        <StyledCol col={12} noGutter={true}>
          <Step5 />
        </StyledCol>
        <StyledCol ref={somos} col={12} noGutter={true}>
          <Step6 language={language}/>
        </StyledCol>
        <StyledCol col={12} noGutter={true}>
          <Step7  language={language}/>
        </StyledCol>
        <StyledCol col={12} noGutter={true}>
          <Step8 />
        </StyledCol>
        <StyledCol col={12} noGutter={true}>
          <Step10 />
        </StyledCol>
      </StyledRow>
    </Layout>
  )
}

export default IndexPage

export const query = graphql`
  query($language: String!) {
    locales: allLocale(
      filter: { ns: { in: ["common", "index"] }, language: { eq: $language } }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`
